import React, { Component } from "react";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import Skeleton from "react-loading-skeleton";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import notrespond from "../../Images/500.svg";
import Axios from "axios";
import { berhasil, gagal } from "../../Alert";
import { getEditPindahBarang } from "../../actions/pindahBarangAction";

const { SearchBar } = Search;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class EditForm extends Component {
  constructor(props) {
    super(props);
    this.from = props.match.params.from;
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
        },
        {
          dataField: "namaAktor",
          text: "Nama Aktor",
        },
        {
          dataField: "ke",
          text: "Ke",
        },
        {
          dataField: "bruto",
          text: "Bruto",
        },
        {
          dataField: "netto",
          text: "Netto",
        },
        {
          dataField: "rp",
          text: "Ongkos(Rp)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div>
                <button
                  className="btn btn-warning"
                  onClick={() => this.simpanEditBarang(row.noFaktur)}
                >
                  <i className="fa fa-edit mr-3"></i>EDIT
                </button>
              </div>
            );
          },
        },
      ],
      listPindahBarang: [],
    };
  }

  componentDidMount() {
    switch (this.from) {
      case "pindahBarang":
        Axios.get(server + "item/dc/pindahbarang/heads?statusValid=OPEN")
          .then((res) =>
            this.setState({
              listPindahBarang: res.data,
            })
          )
          .catch((res) => console.log(res));
        break;
      case "terimaSupplier":
        Axios.get(
          server +
            "spp/dc/trm/details?tglFrom=2020-12-01&tglTo=2021-01-30&statusValid=OPEN"
        )
          .then((res) =>
            this.setState({
              listPindahBarang: res.data,
            })
          )
          .catch((res) => console.log(res));
        break;
      case "transaksiPenjualan":
        Axios.get(server + "cst/dc/pjl/heads?statusValid=OPEN")
          .then((res) =>
            this.setState({
              listPindahBarang: res.data,
            })
          )
          .catch((res) => console.log(res));
        break;
      case "returnSupplier":
        Axios.get(server + "spp/dc/rtr/details", {
          params: {
            tglFrom: "2020-01-01",
            tglTo: "2022-01-01",
            statusValid: "OPEN",
          },
        })
          .then((res) =>
            this.setState({
              listPindahBarang: res.data,
            })
          )
          .catch((res) => console.log(res));
        break;
      default:
        break;
    }
  }

  simpanEditBarang(data) {
    switch (this.from) {
      case "pindahBarang":
        Axios.get(server + "item/dc/pindahbarang/detail?noFaktur=" + data)
          .then((res) => this.tambahLocal(res))
          .then((res) => berhasil("Berhasil"))
          .then(() => window.history.back())
          .catch(() => gagal("Ambil Data Gagal, Mohon Periksa Koneksi Anda"));
        break;
      case "transaksiPenjualan":
        Axios.get(server + "cst/dc/pjl/details?noFaktur=" + data)
          .then((res) => this.tambahLocal(res))
          .then(() => berhasil("Berhasil"))
          .then(() => window.history.back())
          .catch(() => gagal("Ambil data Gagal, Mohon Periksa Koneksi Anda"));
        break;
      case "terimaSupplier":
        Axios.get(server + "spp/dc/trm/detail?noFaktur=" + data)
          .then((res) => this.tambahLocal(res))
          .then(() => berhasil("Berhasil"))
          .then(() => window.history.back())
          .catch(() => gagal("Ambil data Gagal, Mohon Periksa Koneksi Anda"));
        break;
      case "returnSupplier":
        Axios.get(server + "spp/dc/rtr/detail?noFaktur=" + data)
          .then((res) => this.tambahLocal(res))
          .then(() => berhasil("Berhasil"))
          .then(() => window.history.back())
          .catch(() => gagal("Ambil data Gagal, Mohon Periksa Koneksi Anda"));
        break;
      default:
        break;
    }
  }

  tambahLocal(res) {
    switch (this.from) {
      case "pindahBarang":
        localStorage.setItem(
          "pindahBarang",
          JSON.stringify(res.data.barangDetails)
        );
        localStorage.setItem("editPindahBarang", JSON.stringify(res.data));
        localStorage.setItem("noFaktur", res.data.noFaktur);
        this.props.dispatch(getEditPindahBarang());
        break;
      case "terimaSupplier":
        localStorage.setItem(
          "TerimaSupplier",
          JSON.stringify(res.data.barangDetails)
        );
        localStorage.setItem("noFaktur", res.data.noFaktur);
        localStorage.setItem("tanggalBarang", res.data.tanggalBarang);
        localStorage.setItem("tanggal", res.data.tanggal);
        localStorage.setItem("totalNW", res.data.footerItem.totalNettoExt);
        localStorage.setItem("kodeSupplier", res.data.kodeSupplier);
        localStorage.setItem("noBon", res.data.noBon);
        this.props.dispatch(getEditPindahBarang());
        break;
      case "transaksiPenjualan":
        localStorage.setItem(
          "transaksiPenjualan",
          JSON.stringify(res.data.barangDetails)
        );
        localStorage.setItem("noFaktur", res.data.noFaktur);
        localStorage.setItem("noBon", res.data.noBon);
        localStorage.setItem("lokasi", res.data.kodeLokasi);
        localStorage.setItem("namaToko", res.data.namaToko);
        localStorage.setItem("kodeSales", res.data.kodeSales);
        localStorage.setItem("kodeCustomer", res.data.kodeCustomer);
        localStorage.setItem("tanggal", res.data.tanggal);
        localStorage.setItem("discPr", res.data.headDiscount.discPr);
        localStorage.setItem("discGr", res.data.headDiscount.discGr);
        localStorage.setItem(
          "totalNettoExt",
          res.data.headDiscount.totalNettoExt
        );
        break;
      case "returnSupplier":
        localStorage.setItem(
          "ReturnSupplier",
          JSON.stringify(res.data.barangDetails)
        );
        localStorage.setItem("noBon", res.data.noBon);
        break;
      default:
        break;
    }
  }
  render() {
    return (
      <div className="container-fluid">
        <ol className="breadcrumb mb-2">
          <button
            type="button"
            className="btn btn-secondary"
            onClick={() => window.history.back()}
          >
            <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
          </button>
        </ol>
        <div className="card">
          <div className="card-body">
            <div className="col-lg-12 mb-3">
              <h1>Pilih Data Edit</h1>
            </div>
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-3">
                  <Field
                    name="tanggal"
                    label="Tanggal"
                    type="date"
                    component={renderField}
                  />
                </div>
                <div className="col-lg-3">
                  <Field
                    name="dari"
                    label="Dari"
                    type="text"
                    component={renderField}
                  />
                </div>
                <div className="col-lg-3">
                  <Field
                    name="ke"
                    label="Ke"
                    type="text"
                    component={renderField}
                  />
                </div>
                <div className="col-lg-3">
                  <Field
                    name="noBon"
                    label="Nomor Bon"
                    type="text"
                    component={renderField}
                  />
                </div>
                <div className="col-lg-12">
                  {this.state.listPindahBarang ? (
                    <ToolkitProvider
                      keyField="id"
                      data={this.state.listPindahBarang}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="text-left mb-3">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Pencarian"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <hr />
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="Not Respond"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={250} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

EditForm = reduxForm({
  form: "EditForm",
  enableReinitialize: true,
})(EditForm);
export default connect()(EditForm);
